import axios from "axios";
const axiosInstance = axios.create({
  baseURL: "https://newsapi.org/v2/",
  headers: { "x-api-key": "1edd742288934699b927933f30ae17d7" },
});

export const fetchSources = async () => {
  try {
    const response = await axiosInstance.get("top-headlines/sources", {
      headers: {
        'Access-Control-Allow-Origin': '*',
      },
      proxy: {
        host: '104.236.174.88',
        port: 3128
      },
      params: {
        category: "technology",
        country: "us",
        language: "en",
      },
    });
    return response.data.sources;
  } catch (err) {
    throw err;
  }
};

export const fetchArticles = async (sources = [], keyword = "") => {
  const params = {
    pageSize: 30,
    qInTitle: keyword,
    sources,
  };
  try {
    const response = await axiosInstance.get("everything", {
      params,
    });
    return response.data.articles;
  } catch (err) {
    throw err;
  }
};
