import { useLocation } from "react-router-dom";

const Article = () => {
  const { state } = useLocation();
  const { title, url, content, description } = state;

  return (
    <div>
      <h2>{title}</h2>
      <img src={url} alt='no img'/>
      <p>{content}</p>
      <p>{description}</p>
    </div>
  );
};

export default Article;
