import React, { useEffect, useState } from "react";
import Article from "../../components/article/Article";
import Loader from "../../components/loader/Loader";
import Cards from "../../components/source/Cards";
import { fetchArticles, fetchSources } from "../../services";

const Home = () => {
  const [sources, setSources] = useState([]);
  const [CardIds, setCardIds] = useState([]);
  const [isCardsLoading, setisCardsLoading] = useState(false);
  const [articles, setArticles] = useState([]);
  const [isLoadingArticles, setIsLoadingArticles] = useState(false);

  const handleArticlesFetching = async (CardIds, keyword = "") => {
    try {
      setIsLoadingArticles(true);
      const articles = await fetchArticles(CardIds, keyword);
      setArticles(articles);
      setIsLoadingArticles(false);
    } catch (ex) {
      throw ex;
    }
  };

  useEffect(() => {
    setisCardsLoading(true);
    fetchSources()
      .then((sources) => {
        setSources(sources);
        setisCardsLoading(false);
      })
      .catch(() => setisCardsLoading(false));
  }, []);

  useEffect(() => {
    if (sources) {
      const CardIds = sources.map((source) => source.id);
      setCardIds(CardIds);
      handleArticlesFetching(CardIds);
    }
  }, [sources]);

  const filterSource = (id) => {
    handleArticlesFetching([id]);
  };

  const search = (e) => {
    const value = e.target.value;
    handleArticlesFetching(CardIds, value);
  };

  return (
    <div className="container">
      <div className="content">
        {isCardsLoading ? (
          <Loader />
        ) : (
          <div>
            <div className="row">
              {sources.map((source) => (
                <Cards
                  title={source.name}
                  id={source.id}
                  key={source.id}
                  onClick={filterSource}
                />
              ))}
            </div>
            <div id="gradient">
            <form class="searchbox"> 
              <input onChange={search} placeholder="search" className="input" />
              <button type="submit" value="search">&nbsp;</button>
              </form>
            </div>
            <div>
              {isLoadingArticles || !sources.length ? (
                <Loader />
              ) : (
                <div className="articles-container">
                  {articles.length ? (
                    articles.map((article, i) => (
                      <Article
                        key={i}
                        id={i}
                        title={article.title}
                          url= {article.urlToImage}
                        content={article.content}
                        description={article.description}
                      />
                    ))
                  ) : (
                    <p>No article</p>
                  )}
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Home;
