import React from "react";
import { Link } from "react-router-dom";

const Article = (props) => {
  const { title, id, url, content, } = props;

  return (
    <div className="card">
      <div className="row">
        <img src={url} className="image" alt="icon"/>
        <div>
          <h4>{title}</h4>
          <p>{content}</p>
        </div>
      </div>

      <Link
        to={{
          state: props,
          pathname: `/article/${id}`,
        }}
        className="note"
        onClick={(e) => e.stopPropagation()}
      >
        See more
      </Link>
    </div>
  );
};

export default Article;
